* {
  font-family: 'Courier New', Courier, monospace;
}

body {
  padding:60px;
}

input {
  font-size:1.3rem;
  width:100%;
}

.card-grid {
  display:flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 0.3rem;
  margin-bottom: 5px;
  margin-right: 5px;
  overflow: hidden;
}

.card p {
  padding:5px;
  text-align: center;

}

.card img {
  max-height:250px;
}